*
{
    margin: 0;
    padding: 0;
}
body{
    background-image: url("https://media.proprofs.com/images/QM/user_images/2503852/New%20Project%20(70)(271).jpg"); 
    background-repeat: no-repeat;
    background-size: cover;
    background-position: fixed;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
 
}
.title
{
   margin-left: 30%;
   font-size: 50px;
   font-family: jackler West;
   color: rgb(239, 101, 168);
   text-decoration: line-through;
}
.app
{
    background: linear-gradient(4deg,#ff0000 0%, #3a00ff 40%, rgba(228,255,0,0.95) 80%);
    width:70%;
    min-height: 200px;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0,0,0,0.75);
    display: flex;
    justify-content: space-evenly;
    font-weight: bold;
    margin-left: 12%;
    margin-top: 20px;
}
.Question-section
{
    position: relative;
    width: 100vh;

}
.question-text
{
    color: white;
    font-size: 30px;
}
.Answer-section
{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 50%;
   
    
}
button
{
    width: 100%;
    font-size: 17px;
    color: white;
    font-weight: bold;
    background: linear-gradient(90deg,#050505 20%, #38d038 80%);
    border-radius: 15px;
    border-radius: 3px solid rgb(4, 4, 4);
    justify-content: flex-start;
    cursor: pointer;
}
.score-section
{
    display: block;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    color: white;

}